import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import {Link} from 'react-router-dom';
import Input from "../ui/formItem/input/Input";
import Select from "../ui/formItem/select/Select";
import {get} from 'lodash';

require('./style.scss')

class SearchBar extends React.Component {
	constructor(props) {
		super(props);
	}

	handleChange = (field, val) => {

	}

	render() {
		const viewMode = get(this.props, 'match.params.viewMode', 'list')
		return (
      <div className="searchBar">
				<div className="largeContainer">
          <div className="searchBar__container">
            <div className="searchBar__column">
							<Input
								label={'Dove'}
								value={'Qua Intorno'}
								defaultValue={'Qua intorno'}
								field={'where'}
								handleChange={this.handleChange}
							/>
						</div>
            <div className="searchBar__column">
              <Input
                label={'Cosa'}
                value={'Tutto'}
								defaultValue={'Tutto'}
                field={'what'}
                handleChange={this.handleChange}
              />
						</div>
            <div className="searchBar__column">
              <Select
                label={'Quando'}
                value={'week'}
                field={'When'}
                handleChange={this.handleChange}
              />
						</div>
            <div className="searchBar__icon">
              <i className="fa fa-search fa-lg"/>
						</div>
						{viewMode === 'map' ?
              <Link to={'/search/main/list'} className="searchBar__icon inverted">
                <i className="fa fa-list fa-lg"/>
              </Link>:
              <Link to={'/search/main/map'} className="searchBar__icon inverted">
                <i className="fa fa-map fa-lg"/>
              </Link>
						}
					</div>
				</div>
      </div>
		)
	}
}

SearchBar.propTypes={
}

function mapStateToProps(state) {
	return {
	}
}

export default connect( mapStateToProps,null )(SearchBar)


