import React from 'react'
import PropTypes from 'prop-types';
import {get} from "lodash";
import {Link} from 'react-router-dom';
import Event from "../../../components/event/Event";

export default class ListView extends React.Component {
	constructor(props) {
		super(props)
    this.mediaQuerySetting = {
      "PHONE": {column: 1},
      "TABLET_PORTRAIT": {column: 2},
      "TABLET_LANDSCAPE": {column: 3},
      "DESKTOP": {column: 3},
      "LARGE_DESKTOP": {column: 4},
    }
    this.state = {
      elementsInColumn: []
    }
	}

  getItemsAsMasonry = () => {
	  const breakpoint = get(this.props.breakpoint, 'value')
    const events = get(this.props.eventList, 'events')
    if(
      breakpoint === undefined || events === undefined
    ) return []

    const arrayList = Object.values(this.props.eventList.events)
    const columns = get(this.mediaQuerySetting, `${this.props.breakpoint.value}.column`, 3)
    let myArrays = []
    for (let a = 0; a < columns; a++) {
      myArrays[a] = []
      for (let i = a; i < arrayList.length-1; i+=3){
        myArrays[a].push({...arrayList[i]})
      }
    }
    return myArrays
  }

  render() {
		return (
      <div className="eventsList">
        {
          this.getItemsAsMasonry().map((eventColumn, col) => {
            return(
              <div
                key={`column_${col}`}
                className="eventsList__column">
                {
                  eventColumn.map((item, index) => {
                    return(
                      <div
                        key={index}
                        className="eventsList__column">
                        <Link
                          to={`/content/${item.id}`}>
                          <div
                            key={index}
                            className="eventsList__item">
                            <Event
                              event={item}
                            />
                          </div>
                        </Link>
                      </div>
                    )
                  })
                }
              </div>
            )
          })
        }
      </div>
		)
	}
}

ListView.propTypes={
  eventList: PropTypes.object,
  breakpoint: PropTypes.object,
}


