import React from 'react'
import PropTypes from 'prop-types';

require('./style.scss')

const hourIcon = require('../../../public/images/clock-circular-outline.png')
const genderIcon = require('../../../public/images/gender.png')
const crowdIcon = require('../../../public/images/people.png')

const MainInfo = (props) => {
	return (
		<div className="mainInfo">
      <div className="mainInfo__item">
        <img src={hourIcon}/>
        <div className="mainInfo__text">
          10:30
        </div>
      </div>
      <div className="mainInfo__item">
        <img src={genderIcon}/>
        <div className="mainInfo__text">
          50/50
        </div>
      </div>
      <div className="mainInfo__item">
        <img src={crowdIcon}/>
        <div className="mainInfo__text">
          crowded
        </div>
      </div>
    </div>
	)
}

MainInfo.propTypes={

}

export default MainInfo


