import {cloneDeep, slice} from 'lodash';

const INIT = 'eventList/INIT'
const SET = 'eventList/SET'

const initialState = {
  events: []
}

export function init() {
	return {
		type: INIT
	}
}

export function set(events) {
  return {
    type: SET,
		payload:{
    	events: events
		}
  }
}


export default function eventList(state = cloneDeep(initialState), action) {
	switch (action.type) {
		case INIT:
			return {}
		case SET:
		  let newState = cloneDeep(state)
			return {
		  	newState,
				events: action.payload.events
      }
		default:
			return state;
	}
}


