import React from 'react'
import PropTypes from 'prop-types';

// UTILS
import beautifyDistance from '../../../utils/beautifyDistance';

require('./style.scss')

const PlaceInfo = ({nameVenue, distance}) => {

	return (
    <div className="placeInfo">
      <h3 className="placeInfo__place">
        <i className="fa fa-map-pin"/> {nameVenue}
      </h3>
      <div className="placeInfo__distance">
        <i className="fa fa-road"/>
        {beautifyDistance(distance)}
      </div>
    </div>
	)
}

PlaceInfo.propTypes={
  nameVenue: PropTypes.string,
  distance: PropTypes.number,
}

export default PlaceInfo


