import React from 'react'
import {connect} from 'react-redux';

// UTILS
import {get, find} from "lodash";
import {mapEvent} from '../../utils/mapEvents';

// FETCH
import getEvent from '../../fetch/getEvent';

// COMPONENTS
import Event from "../../components/event/Event";
import Suggestions from "../../components/suggestions/Suggestions";
import MapContainer from "../../components/mapContainer/MapContainer";

require('./style.scss')

class ContentDetails extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      event: undefined
    }
  }

  componentDidMount() {
    this.root = this.refs.container
    const id = get(this.props, 'match.params.id')
    getEvent({
      id: id,
      callback: this.onGetEvent
    })
  }

  onGetEvent = (result) => {
    this.setState({
      event: mapEvent(result)
    })
  }

  getEventById = (id) => {
    const events = get(this.props.eventList, 'events')
    return find(events, event => {
      return event.id === id
    }) || {}
  }

  render() {
    const {event} = this.state
    return (
      <div className="container">
        {this.state.event !== undefined &&
          <div className="eventPage">
            <div className="eventPage__left">
              <Event
                event={event}
              />
            </div>
            <div className="eventPage__right">
              <div className="eventPage__right-section">
                <div className="eventPage__right-map">
                  <MapContainer/>
                </div>
              </div>
              <div className="eventPage__right-section">
                <h2 className='eventPage__right-title'>Dove mangiare qua intorno...</h2>
                <Suggestions/>
              </div>
            </div>
          </div>
        }
      </div>
    )
  }
}

ContentDetails.propTypes = {}


function mapStateToProps(state) {
  return {
    eventList: state.rootReducer.eventList
  }
}

export default connect(mapStateToProps)(ContentDetails)


