import React from 'react'
import {connect} from 'react-redux';
import {get, filter} from 'lodash';

// DUCKS
import {set as setEventList} from '../../reducers/ducks/eventList';

// UTILS
import mapEvents from '../../utils/mapEvents';

// FETCH
import getEvents from '../../fetch/getEvents';

// COMPONENTS
import ListView from "./listView/ListView";
import MapView from './mapView/MapView';


require('./listView/style.scss')

class SearchResults extends React.Component {
  constructor(props) {
    super(props)
  }

  componentDidMount() {
    const eventsLoaded = get(this.props.eventList, 'events', [])
    if(eventsLoaded.length === 0){
      getEvents({
        lat: 45.4642040,
        lng: 9.1899820,
        callback: this.onGetEvents
      })
    }
  }

  onGetEvents = (results) => {
    const mappedEvents = mapEvents(results)
    this.props.dispatch(setEventList(mappedEvents))
  }
  
  render() {
    const query = get(this.props, 'match.params.query')
    const viewMode = get(this.props, 'match.params.viewMode', 'list')
    return (
      <div >
        <div className="largeContainer">
          {viewMode === 'list' ?
            <ListView
              eventList={this.props.eventList}
              breakpoint={this.props.breakpoint}
            /> :
            <MapView/>
          }

        </div>
      </div>
    )
  }
}

SearchResults.propTypes = {}

function mapStateToProps(state) {
  return {
    eventList: state.rootReducer.eventList,
    breakpoint: state.rootReducer.breakpoint,
  }
}

export default connect(mapStateToProps)(SearchResults)


