const getEvents = ({lat, lng, callback}) => {
  console.log("FETCH")
  fetch(`${API_PATH}/events?ll=${lat},${lng}&start=0&limit=50&d=10`)
    .then((response) => response.json())
    .then((responseJson) => {
      console.log("FETCHED")
      callback(responseJson)
    })
    .catch((error) => {
      console.error(error);
    });
}

export default getEvents



