import React from 'react'
import PropTypes from 'prop-types'
import {get} from 'lodash';

// CONST
import MainActions from "./MainActions";
import PlaceInfo from "./placeInfo/PlaceInfo";
import DateInfo from "./dateInfo/DateInfo";
import MainInfo from "./mainInfo/MainInfo";

require('./style.scss')

export default class Event extends React.Component {
	constructor(props) {
		super(props)
	}
	render() {
		const {title, imageSrc, nameVenue, distance, description} = this.props.event
		return (
			<div className="event">
        <div className="event__above">
          <PlaceInfo
            nameVenue={nameVenue}
            distance={distance}
          />
        </div>
        <img className="event__cover" src={imageSrc}/>
				<div className="event__caption">
          <div className="event__heart">
            <i className="fa fa-heart"/>
          </div>
          <h1 className="event__title">{title}</h1>
				</div>
        { description !== undefined &&
          <div className="event__description">
            <p>{description}</p>
          </div>
        }
        <div className="event__infoCaption">
          <div className="event__date">
            <DateInfo/>
          </div>
          <div className="event__info">
            <MainInfo/>
          </div>
        </div>
        <MainActions/>
			</div>
		)
	}
}

Event.propTypes={
  event: PropTypes.object,
}


