import {get} from 'lodash';

const eventPlaceholderImages = [
  require('../public/placeholder/event.jpeg'),
  require('../public/placeholder/event2.jpeg'),
  require('../public/placeholder/event3.jpeg'),
  require('../public/placeholder/event4.jpeg'),
]

export const mapEvent = (event) => {
  return {
    title: get(event, 'title', ''),
    nameVenue: get(event, 'nameVenue', ''),
    distance: get(event, 'distance', 0),
    id: get(event, 'id'),
    description: get(event, 'description'),
    imageSrc: get(event, 'link', eventPlaceholderImages[Math.floor(Math.random() * 3)])
  }
}

const mapEvents = ({events}) => {
  return events.map(event => {
    return mapEvent(event)
  })
}

export default mapEvents



