import {API_PATH} from '../utils/_const';

const getEvent = ({id, callback}) => {
  console.log("FETCH")
  fetch(`${API_PATH}/events/${id}`)
    .then((response) => response.json())
    .then((responseJson) => {
      console.log("FETCHED")
      callback(responseJson)
    })
    .catch((error) => {
      console.error(error);
    });
}

export default getEvent



